<template>
  <div>
    <h3>Редактирование времени</h3>
    <ul class="editTime">
      <li v-for="time in timeArr">
        <sui-checkbox  radio :value="time" :name="time" v-model="selectedTime" v-on:change="selectTask(time)" />
        <span class="time">{{time}}</span>
      </li>
    </ul>
    <span><b>{{selectedTime}}</b></span>
    <span><b>{{selectedItems}}</b></span>
  </div>
</template>

<script>
  import {eventEmitter} from './main'
export default {
  data(){
    return{
      timeArr: [],
      selectedItems: [],
      selectedTime: ''
    }
  },
  mounted() {
      //параметры в пропсы?
      //ошибка генерации времени
      let i;
      for(i=1000; i<=1330; i+=15){

        let x = String(i);
        if(x.length<4){x = '0'+x};
        if(x[0]<3 && x[2]<6){
          let y = x[0]+x[1]+':'+x[2]+x[3];
          this.timeArr.push(y);
        }
      }
  },
  methods: {
    selectTask(time){
      this.selectedItems.map((item) => {
        item.time = time;
        this.$store.dispatch('patchItem',item)
      });
      eventEmitter.$emit('taskSelected', time)
    }
  },
  created() {
    eventEmitter.$on('timeSelected', (arr) => {
      this.selectedItems = arr;
      this.selectedTime = '';
      if(arr.length == 1){
        this.selectedTime = arr[0].time
      }
    })
  }
}
</script>

<style>
.editTime{
  -webkit-column-count: 4;
  -moz-column-count: 4;
  column-count: 4;
  margin-left: 0;
  padding-left: 0;
}
.editTime li{
  list-style-type: none;
}
.time{
  font-size: 18px;
}
</style>



